@import '../../../scss/mixins';

.inputText {
  @include hbFormType;
  height: 49px;
  background: var(--petrol-80);
  color: #fff;
  font-weight: normal;
  border: 1px solid transparent;
  border-radius: 10px;
  @media screen and (max-width: 480px) {
    height: 51px;
  }
}

.inputText:focus {
  background-color: var(--petrol-80);
  color: #fff;
  outline: 0;
  box-shadow: none;
}

.inputText::placeholder {
  color: var(--nightblue-50);
}

.inputText:-webkit-autofill,
.inputText:-webkit-autofill:hover,
.inputText:-webkit-autofill:focus,
.inputText:-webkit-autofill,
.inputText:-webkit-autofill:hover,
.inputText:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  background-color: var(--petrol-80);
  -webkit-text-fill-color: #ffffff;
  box-shadow: var(--petrol-80);
  transition: background-color 5000000s ease-in-out 0s;
}

.email {
  text-align: left;
}

.inputPassword {
  margin-bottom: 0 !important;
  position: relative;
  text-align: left;
  .inputTextPassword {
    background-color: var(--petrol-80);
    color: #fff;
    border-radius: 10px;
    border: none;
    height: 51px;
  }

  .inputTextPassword:focus {
    background-color: var(--petrol-80);
    color: #fff;
    outline: 0;
    box-shadow: none;
  }

  .inputTextPassword::placeholder {
    color: var(--nightblue-50);
  }

  .inputTextPassword:-webkit-autofill,
  .inputTextPassword:-webkit-autofill:hover,
  .inputTextPassword:-webkit-autofill:focus,
  .inputTextPassword:-webkit-autofill,
  .inputTextPassword:-webkit-autofill:hover,
  .inputTextPassword:-webkit-autofill:focus,
  select:-webkit-autofill,
  select:-webkit-autofill:hover,
  select:-webkit-autofill:focus {
    background-color: var(--petrol-80);
    -webkit-text-fill-color: #ffffff;
    box-shadow: var(--petrol-80);
    transition: background-color 5000000s ease-in-out 0s;
  }

  .iconPassword {
    position: absolute;
    right: calc(0.375em + 0.1875rem);
    top: calc(28px - 0.5em);
    font-size: 20px;
    color: #fff;
  }
}

.label {
  @include h6;
  text-align: center;
  a {
    color: var(--aquamarin-80);
    cursor: pointer;
  }
  &.lbCreateAccount {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &.lbForgotPwd {
    margin-top: 8px;
    @media screen and (max-width: 860px) {
      margin-top: 16px;
    }
  }
}

.btnSubmit {
  @include hbButtonText;
  margin: 16px 0;
  padding: 0;
  width: 100%;
  height: 45px;
  border-radius: 12px !important;
  @media screen and (max-width: 860px) {
    margin: 48px 0 18px;
  }
  @media screen and (max-width: 480px) {
    margin: 30px 0 17px;
  }
}

.register_link {
  color: var(--aquamarin-80);
  margin-left: 8px;
}

.header {
  background: var(--petrol-100);
  top: 0;
  border: 0;
  justify-content: center;
  margin-top: 23px;
}

.cardTitle {
  margin-bottom: 16px;
  text-align: center;
  @media screen and (max-width: 860px) {
    margin-bottom: 48px;
    text-align: left;
  }
  @media screen and (max-width: 480px) {
    margin-bottom: 30px;
  }
}

.lastFormGroup {
  margin-bottom: 0 !important;
}
